<template>
     <div class="wrapper"  >
        <div class="container  ">
           
        <h3>家中幾個人吃飯？</h3>
        <div class="d-flex flex-wrap justify-content-center">
            <div class="selector" v-for="item in people" :key="item.id">
                <div class="form-check">
                    <input class="form-check-input select-input" :checked="peopleSelected == item.id" :value="item.id" v-model="peopleSelected" type="radio" name="flexRadioDefaultpeople" :id="'flexRadioDefaultpeople'+item.id">
                    <label class="form-check-label select-label" :for="'flexRadioDefaultpeople'+item.id" tabindex="-1">
                    {{item.id}} 人
                    </label>
                </div>
            </div>
           
        </div>

        <h3>一週有幾天在家煮飯呢？</h3>

        <div class="d-flex flex-wrap justify-content-center">
            <div class="selector" v-for="item in days" :key="item.id">
                <div class="form-check">
                    <input class="form-check-input select-input" :checked="daysSelected == item.id" :value="item.id" v-model="daysSelected" type="radio" name="flexRadioDefaultdays" :id="'flexRadioDefaultdays'+item.id">
                    <label class="form-check-label select-label" :for="'flexRadioDefaultdays'+item.id">
                    {{item.id}} 天
                    </label>
                </div>
            </div>
           
        </div>

        <h3>每天都在家吃多少餐呢？</h3>

        <div class="d-flex flex-wrap justify-content-center">
            <div class="selector" v-for="item in meal" :key="item.id">
                <div class="form-check">
                    <input class="form-check-input select-input" :checked="mealSelected == item.id" :value="item.id" v-model="mealSelected" type="radio" name="flexRadioDefaultmeal" :id="'flexRadioDefaultmeal'+item.id">
                    <label class="form-check-label select-label" :for="'flexRadioDefaultmeal'+item.id">
                    {{item.id}} 餐
                    </label>
                </div>
            </div>
           
        </div>
    </div>
    <!-- <button @click="showAlert">Hello world</button> -->
    <div  :href="linkToLocate">
       <a class="totalRice" :href="linkToLocate" target="_parent">
           <div class="fw-lighter">查看適合您的訂閱方案</div>
            <div>
                 
            <span class="need"> {{calcRice}}</span>  包 / 月
        </div>
        </a>
    </div>
    </div>
    
    
</template>

<script>
export default {
  name: 'HelloWorld',
    computed:
  {
    calcRice(){
        let vm =this;
        let total =(vm.ricePerPerson* (vm.daysSelected *vm.mealSelected)*vm.peopleSelected)*4/0.6;
        return Math.ceil(total) ;
    },
    linkToLocate(){
        let vm=this;
        if (vm.calcRice>0 && vm.calcRice <5){
          //to4
            return "https://agoodbarn.com/products/48";
        }
        else if (vm.calcRice>=4 && vm.calcRice< 7) {
          //to6
            return "https://agoodbarn.com/products/49";
        }
        else if (vm.calcRice>=7 && vm.calcRice< 9) {
          //to8
            return "https://agoodbarn.com/products/50";
        } else if (vm.calcRice>=9 && vm.calcRice< 11) {
          //to10
            return "https://agoodbarn.com/products/55";
        }
         else if (vm.calcRice>=11 && vm.calcRice< 13) {
            //to12
            return "https://agoodbarn.com/products/52";
        }
         else if (vm.calcRice>=13 && vm.calcRice< 15) {
            //to14
            return "https://agoodbarn.com/products/53";
        }
        else if (vm.calcRice>=15 && vm.calcRice< 17) {
            //to16
            return "https://agoodbarn.com/products/54";
        }
         else if (vm.calcRice==18 ) {
            //to18
            return "https://agoodbarn.com/products/51";
        }
        
        else if (vm.calcRice>18) {
          this.showAlert();
           return  "https://agoodbarn.com/search?tag=%E5%AE%9A%E6%9C%9FGO";
        } else{
            return "https://agoodbarn.com/search?tag=%E5%AE%9A%E6%9C%9FGO";
        }
    }
  },
 
  methods: {
    showAlert() {
      // Use sweetalert2
      this.$swal({
  icon: 'info',
  title: '哎呀',
  html:
    '我們現階段還沒新增此方案</br> ' +
    '<strong>馬上聯繫客服</strong></br>獲取專屬於你的方案吧～' ,
   showCancelButton: true,
  confirmButtonColor: '#c76c61',
  confirmButtonText: '聯繫客服',
  cancelButtonColor: '#C0C0C0',
  cancelButtonText: '不需要'
}).then(okay => {
   if (okay.value) {
     if(this.$isMobile()){
       window.parent.location.href  = "https://m.me/agoodbarn";
     }else{
       window.parent.location.href  = "https://www.facebook.com/agoodbarn";
     }
  }
});
    },
  },
  data:() => {
    return{

    message: 'Hello Vue!',
    peopleSelected:1,
    daysSelected:1,
    ricePerPerson:0.075,
    mealSelected:1,

    product:0.6,
    people:[
        {id:1},
        {id:2},
        {id:3},
        {id:4},
        {id:5},
    ],
    days:[
        {id:1},
        {id:2},
        {id:3},
        {id:4},
        {id:5},
        {id:6},
        {id:7},
    ],
    meal:[
        {id:1},
        {id:2},
        {id:3},
       
    ],
  }
    }

}
</script>

